<template>
  <div class="hello collection">
    <div class="first-top">
      <img src="../../../static/img/date.png" style="margin: 0px 3px 4px 18px"/>
      <span class="first-top-text">所得月份</span>
      <el-date-picker :value="month" type="date" placeholder="选择日期" value-format="yyyy-MM-dd"></el-date-picker>
      <!-- <img src="../../../static/img/first-date-info.png" style="margin-bottom: 4px"> -->
    </div>
    <div class="first-mid">
      <div class="content">
        <div class="content-top">
          <div style="display: flex;align-items: center">
            <span class="content-top-text-blue"></span>
            <span style="font-size: 16px; margin-left: 10px">常用功能</span>
          </div>
          <div class="content-wrap">
            <router-link to="/collection" data-title="人员信息采集" href="javascript:;">
              <div class="wrap-div">
                <img src="../../../static/img/first1.png" alt/>
                <div>
                  <div class="wrap-title-text">人员信息采集</div>
                  <!--<div class="wrap-detail">0人实名认证未通过</div>-->
                  <!--<div class="wrap-detail">1人未报送</div>-->
                </div>
              </div>
            </router-link>

            <router-link to="/payCollectionChild" data-title="专项附加扣除信息采集" href="javascript:;">
              <div class="wrap-div">
                <img src="../../../static/img/first2.png" alt/>
                <div>
                  <div class="wrap-title-text">专项附加扣除信息采集</div>
                  <div class="wrap-detail">子女教育支出，住房租金支出等信息采集</div>
                </div>
              </div>
            </router-link>

            <a
              data-href="/AccountantOperate/WithholdingTax/index.html"
              data-title="综合所得申报"
              href="javascript:;"
            >
              <div class="wrap-div">
                <img src="../../../static/img/first4.png" alt/>
                <div>
                  <div class="wrap-title-text">综合所得申报</div>
                  <div class="wrap-detail">工资薪金、劳务报酬、稿酬、特许权使用费等月度申报</div>
                </div>
              </div>
            </a>

            <!--        <a data-href="/AccountantOperate/Submission/index.html" data-title="分类所得申报" onclick="Hui_admin_tab(this)"-->
            <!--           href="javascript:;">-->
            <div class="wrap-div" @click="showNull">
              <img src="../../../static/img/first3.png" alt/>
              <div>
                <div class="wrap-title-text">分类所得申报</div>
                <div class="wrap-detail">利息股息红利、财产租赁、财产转让等月度申报</div>
              </div>
            </div>
            <!--        </a>-->

            <!--        <a data-href="/AccountantOperate/PayTax/index.html" data-title="非居民所得申报" onclick="Hui_admin_tab(this)">-->
            <div class="wrap-div" @click="showNull">
              <img src="../../../static/img/first3.png" alt/>
              <div>
                <div class="wrap-title-text">非居民所得申报</div>
                <div class="wrap-detail">工资薪金、劳务报酬、稿酬、特许全使用等费用申报</div>
              </div>
            </div>
            <!--        </a>-->
            <router-link to="/taxThree" data-title="税款缴纳">
              <div class="wrap-div">
                <img src="../../../static/img/first5.png" alt/>
                <div>
                  <div class="wrap-title-text">税款缴纳</div>
                  <div class="wrap-detail">个人所得税税款在线缴纳</div>
                </div>
              </div>
            </router-link>
          </div>
        </div>
        <div class="content-bottom">
          <div style="display: flex;align-items: center">
            <span class="content-top-text-blue"></span>
            <span style="font-size: 16px; margin-left: 10px">待处理事项 （0）</span>
          </div>
          <div class="content-news" style="margin-top: 15px;text-align: center">暂无待处理事项</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  components: {},
  computed: {
    month() {
      return this.$store.state.company.company[0]['shenbao_month']
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
  },
  methods: {
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能', '信息', {
        confirmButtonText: '确定'
      })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .hello {
    height: auto;
  }

  .first-top {
    position: relative;
    left: 0px;
    right: 0px;
    height: 42px;
    line-height: 42px;
    background-color: #ffffff;
    text-align: left;
    vertical-align: middle;
  }

  .first-top img {
    vertical-align: middle;
  }

  .first-top-text {
    font-size: 14px;
    font-weight: bolder;
    margin-right: 8px;
  }

  .first-mid {
    position: relative;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
  }

  .content {
    position: relative;
    font-size: 14px;
    line-height: 1.6;
    overflow: hidden;
    text-align: left;
    background-color: #f4f6f9;
    padding: 20px;
    display: flex;
    flex-direction: column;
  }

  .content-top {
    width: 100%;
    height: 295px;
    background-color: #ffffff;
    border-radius: 3px;
    padding: 15px;
    position: relative;
    box-sizing: border-box;
  }

  .content-top-text-blue {
    display: inline-block;
    width: 4px;
    height: 20px;
    background-color: #4285f4;
    border-radius: 5px;
    line-height: 25px;
  }

  .content-wrap {
    position: relative;
    background-color: #ffffff;
    top: 0px;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 5px;
  }

  .wrap-div {
    float: left;
    width: 32%;
    height: 105px;
    margin: 0 5px 10px 5px;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 15px 10px;
    border: solid 1px #f6f7f8;
    border-radius: 3px;
  }

  .wrap-div:hover {
    cursor: pointer;
    border-color: #4285f4;
  }

  .wrap-div img {
    width: 52px;
    height: 52px;
    margin-right: 10px;
  }

  .wrap-title-text {
    font-size: 16px;
    color: #333;
  }

  .wrap-detail {
    font-size: 13px;
    color: #999;
  }

  .content-bottom {
    background-color: #ffffff;
    flex: 1;
    margin-top: 20px;
    border-radius: 3px;
    padding: 15px;
    position: relative;
    box-sizing: border-box;
    min-height: 342px;
  }

  .content-news {
    box-sizing: border-box;
    padding: 5px 10px;
    display: flex;
    justify-content: center;
    font-size: 20px;
    color: #999999;
  }

  .content-wrap {
    width: 100%
  }
</style>
<style>
  .el-input__icon.el-icon-date {
    display: none;
  }

  .el-input--prefix .el-input__inner {
    padding-left: 5px;
    height: 35px;
  }
</style>



